@import '~assets/styles/h3Imports';

.notification {
  border: 1px solid var(--interface-info);
  border-radius: var(--border-radius, 8px);
  box-shadow: var(--interface-info-shadow);
  display: flex;
  margin-bottom: var(--spacing-s);
  padding: 1rem;

  &.no-text {
    align-items: center;
    padding: 0.8325rem 1rem;
  }

  &[data-variant='info'] {
    background-color: var(--interface-info-background);
    border: 1px solid var(--interface-info);
    box-shadow: var(--interface-info-shadow);

    --toast-fill: var(--interface-info);
    --toast-stroke: var(--interface-info);
  }

  &[data-variant='success'] {
    background-color: var(--interface-success-background);
    border-color: var(--interface-success);
    box-shadow: var(--interface-success-shadow);

    --toast-fill: var(--interface-success);
    --toast-stroke: var(--interface-success);
  }

  &[data-variant='warning'] {
    background-color: var(--interface-warning-background);
    border-color: var(--interface-warning);
    box-shadow: var(--interface-warning-shadow);

    --toast-fill: var(--interface-warning);
    --toast-stroke: var(--interface-warning);
  }

  &[data-variant='error'] {
    background-color: var(--interface-error-background);
    border-color: var(--interface-error);
    box-shadow: var(--interface-error-shadow);

    --toast-fill: var(--interface-error);
    --toast-stroke: var(--interface-error);
  }
}

.figure {
  flex: 0 1 2rem;
  margin-right: var(--spacing-xs);

  svg {
    height: auto;
    width: 100%;
  }
}

.notification-text {
  flex: 0 1 calc(100% - 48px);

  > *:first-child {
    margin-top: 0;
  }

  > *:last-child {
    margin-bottom: 0;
  }
}

.title {
  @include h5;

  margin-bottom: var(--spacing-xxxxs);
}

.text {
  @include paragraph;

  margin-bottom: 0;
  margin-top: 0;
}

.buttons {
  margin-left: -0.5rem;
  margin-top: var(--spacing-xxxxs);
}

.links {
  padding-top: var(--spacing-xs);
}
